// Footer

.content-footer {
    padding: 20px 30px;
    background-color: #fff;

    .footer {
        font-size: 90%;
        padding-bottom: 18px;

        .go-right {
            float: right;
        }

        @include screen-mobile {
            text-align: center;

            .go-right {
                float: none;
                margin-top: 15px;
                display: block;
            }
        }
    }
}
