// Controls

.checkbox {
    padding: 10px 0;
    min-height: auto;
    position: relative;

    input[type=checkbox] {
        margin: 0;
        display: none;
        width: 22px;

        + label {
            padding-left: 0px;

            &:before {
                content: $icon-check;
                width: 22px;
                height: 22px;
                display: inline-block;
                margin-right: 10px;
                border: 2px solid $border-color;
                border-radius: 3px;
                font-size: 15px;
                font-family: $icon-font;
                font-weight: 400;
                line-height: 19px;
                vertical-align: bottom;
                text-align: center;
                background: $default-white;
                color: transparent;
                cursor: pointer;
                @include transition(all 0.2s ease-in-out);
            }
        }

        &:checked {

            + label {
                &:before {
                    background: $default-success;
                    border-color: $default-success;
                    color: $default-white;
                }
            }
        }

        &:disabled,
        &.disabled {

            + label {
                opacity: 0.5;

                &:before {
                    cursor: not-allowed;
                }
            }
        }

        &.error {

            + label {

                &:before {
                    border-color: #ffd0d9;
                }
            }
        }
    }

    &.to-do {
        input[type=checkbox] {

            &:checked {

                + label {
                    text-decoration: line-through;
                }
            }
        }
    }

    &.checkbox-primary {
        input[type=checkbox] {
            &:checked {

                + label {
                    &:before {
                        background: $default-primary;
                        border-color: $default-primary;
                        color: $default-white;
                    }
                }
            }
        }
    }

    &.checkbox-info {
        input[type=checkbox] {
            &:checked {

                + label {
                    &:before {
                        background: $default-info;
                        border-color: $default-info;
                        color: $default-white;
                    }
                }
            }
        }
    }

    &.checkbox-warning {
        input[type=checkbox] {
            &:checked {

                + label {
                    &:before {
                        background: $default-warning;
                        border-color: $default-warning;
                        color: $default-white;
                    }
                }
            }
        }
    }

    &.checkbox-danger {
        input[type=checkbox] {
            &:checked {

                + label {
                    &:before {
                        background: $default-danger;
                        border-color: $default-danger;
                        color: $default-white;
                    }
                }
            }
        }
    }
}

