}

.radio,
.checkbox {
    label {
        cursor: pointer;
    }
}

.switch {
    input[type=checkbox] {
        opacity: 0;
        position: absolute;

        + label {
            position: relative;
            display: inline-block;
            @include transition(.4s ease);
            height: 20px;
            width: 35px;
            border: 1px solid $border-color;
            border-radius: 60px;
            cursor: pointer;

            &:before {
                content: "";
                position: absolute;
                display: block;
                @include transition(0.2s cubic-bezier(0.24, 0, 0.5, 1));
                height: 20px;
                width: 35px;
                top: 0;
                left: 0;
                border-radius: 30px;
            }

            &:after {
                content: "";
                position: absolute;
                display: block;
                box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 4px 0px 0 rgba(0, 0, 0, 0.04), 0 4px 9px rgba(0, 0, 0, 0.13), 0 3px 3px rgba(0, 0, 0, 0.05);
                @include transition(0.35s cubic-bezier(0.54, 1.6, 0.5, 1));
                background: darken($default-white, 3%);
                height: 19px;
                width: 19px;
                top: 0px;
                left: 0px;
                border-radius: 60px;
            }
        }

        &:checked {

            + label {

                &:before {
                    background: $default-success;
                    @include transition(width 0.2s cubic-bezier(0, 0, 0, 0.1));
                }

                &:after {
                    left: 16px;
                }
            }
        }

        &:disabled {
            + label {
                opacity: 0.7;
                cursor: not-allowed;
            }
        }
    }

    &.switch-primary {

        input[type=checkbox] {
            &:checked {

                + label {

                    &:before {
                        background: $default-primary;
                    }
                }
            }
        }
    }

    &.switch-info {

        input[type=checkbox] {
            &:checked {

                + label {

                    &:before {
                        background: $default-info;
                    }
                }
            }
        }
    }

    &.switch-warning {

        input[type=checkbox] {
            &:checked {

                + label {

                    &:before {
                        background: $default-warning;
                    }
                }
            }
        }
    }

    &.switch-danger {

        input[type=checkbox] {
            &:checked {

                + label {

                    &:before {
                        background: $default-danger;
                    }
                }
            }
        }
    }
}
