// Themes

.side-nav-dark {

    .side-nav {
        background-color: $side-nav-dark;
        color: $side-nav-dark-font;

        .side-nav-logo {
            border-right: 1px solid transparent;
        }

        .side-nav-inner {

            .side-nav-menu {
                border-right: 1px solid transparent;

                li {

                    a {
                        color: $side-nav-dark-font;
                        font-weight: normal;

                        &:hover,
                        &:focus {

                            .title,
                            .arrow,
                            .icon-holder {
                                color: $default-white;
                            }
                        }
                    }

                    &.dropdown {
                        &.open {

                            > a {
                                .title,
                                .arrow,
                                .icon-holder {
                                    color: $default-white;
                                }
                            }
                        }
                    }
                }

                > li {
                    &.dropdown {

                        ul {
                            &.dropdown-menu {
                                @include background-opacity(darken($side-nav-dark, 5%), 0.5);

                                > li {

                                    > a {
                                        &:hover,
                                        &:focus {
                                            color: $default-white;
                                        }
                                    }

                                    &.active {
                                        > a {
                                            color: $default-white;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

