}


.header-primary-gradient {
    .header {
        background: $gradient-primary;
    }

    .side-nav {
        .side-nav-inner {
            .side-nav-menu {
                li {
                    &.dropdown {
                        > a {
                            &:hover,
                            &:focus {
                                .icon-holder {
                                    color: $default-primary;

                                    i {
                                        &:before {
                                            background: $gradient-primary;
                                            @include gradient-text-masking;
                                        }
                                    }
                                }
                            }
                        }

                        &.open {
                            > a {
                                .icon-holder {
                                    color: $default-primary;

                                    i {
                                        &:before {
                                            background: $gradient-primary;
                                            @include gradient-text-masking;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

