// Tabs

.nav-tabs {
    border-bottom: 1px solid $border-color;

    > li {
        margin-bottom: -2px;

        > a {
            margin-right: 0px;
            line-height: 2.5;
            border-radius: 0;
            color: $default-dark;
            opacity: 0.75;
            border: 0px;
            border-bottom: 2px solid transparent;
            display: block;
            padding: 10px 15px;
            @include transition(all 0.3s ease-in-out);

            &:hover {
                border-color: transparent;
            }

            &:hover,
            &:focus {
                background-color: transparent;
                opacity: 1;
            }
        }

        > a {

            &.active {
                border: 0px;
                background-color: transparent;
                border-bottom: 2px solid $default-primary;
                opacity: 1;

                &:hover,
                &:focus {
                    border: 0px;
                    background-color: transparent;
                    border-bottom: 2px solid $default-primary;
                    opacity: 1;
                }

                &.nav-link {
                    border-bottom: 2px solid $default-primary;
                    color: $default-dark;
                    background-color: transparent;
                }
            }
        }

    }

    .nav-link {
        border-top: 0px;
        border-right: 0px;
        border-left: 0px;

        &:hover,
        &:focus,
        &:active {
            border-color: transparent;
        }
    }
}

.tab-success {

    .nav-tabs {
        > li {
            > a {

                &.active {
                    border-bottom: 2px solid $default-success;

                    &:hover,
                    &:focus {
                        border-bottom: 2px solid $default-success;
                    }
                }
            }
        }
    }
}

